/**
 * 課程 API 端點
 * 簡化版本，只處理課程列表查詢
 */

interface Env {
    DATABASE_URL?: string
}

interface Context {
    request: Request
    env: Env
}

export async function onRequestGet(context: Context): Promise<Response> {
    const { request, env } = context

    try {
        console.log('[Courses] 查詢課程列表')

        // 導入 Neon 數據庫
        const { neon } = await import('@neondatabase/serverless')
        const databaseUrl = env.DATABASE_URL
        if (!databaseUrl) {
            return new Response(
                JSON.stringify({ success: false, message: 'Database URL not configured' }),
                { status: 500, headers: { 'Content-Type': 'application/json', 'Access-Control-Allow-Origin': '*' } }
            )
        }

        const sql = neon(databaseUrl)
        const url = new URL(request.url)

        // 獲取查詢參數
        const page = parseInt(url.searchParams.get('page') || '1', 10)
        const limit = parseInt(url.searchParams.get('limit') || '12', 10)
        const offset = (page - 1) * limit
        const courseType = url.searchParams.get('course_type')
        const search = url.searchParams.get('search')

        // 課程類型映射：前端英文 -> 數據庫中文
        const courseTypeMapping: Record<string, string> = {
            'basic': '基礎課程',
            'advanced': '進階課程',
            'internship': '實習課程'
        }

        // 構建查詢條件
        let whereConditions = ['is_active = true']
        let queryParams: any[] = []
        let paramIndex = 1

        if (courseType && courseTypeMapping[courseType]) {
            whereConditions.push(`course_type = $${paramIndex}`)
            queryParams.push(courseTypeMapping[courseType])
            paramIndex++
        }

        if (search) {
            whereConditions.push(`(title ILIKE $${paramIndex} OR description ILIKE $${paramIndex})`)
            queryParams.push(`%${search}%`)
            paramIndex++
        }

        const whereClause = whereConditions.join(' AND ')

        // 獲取總數
        const countQuery = `SELECT COUNT(*) as count FROM courses WHERE ${whereClause}`
        const countResult = await sql.unsafe(countQuery, queryParams)
        const total = parseInt(countResult[0]?.count || '0', 10)

        // 獲取課程列表
        const coursesQuery = `
      SELECT * FROM courses 
      WHERE ${whereClause}
      ORDER BY created_at DESC 
      LIMIT $${paramIndex} OFFSET $${paramIndex + 1}
    `
        const courses = await sql.unsafe(coursesQuery, [...queryParams, limit, offset])

        // 將數據庫中文課程類型轉換為前端英文類型
        const courseTypeReverseMapping: Record<string, string> = {
            '基礎課程': 'basic',
            '進階課程': 'advanced',
            '實習課程': 'internship'
        }

        const processedCourses = Array.isArray(courses) ? courses.map((course: any) => ({
            ...course,
            course_type: courseTypeReverseMapping[course.course_type] || course.course_type
        })) : []

        console.log('[Courses] 查詢結果:', { total, coursesCount: processedCourses.length })

        return new Response(
            JSON.stringify({
                success: true,
                data: processedCourses,
                meta: {
                    page,
                    limit,
                    total,
                    totalPages: Math.ceil(total / limit)
                }
            }),
            {
                status: 200,
                headers: { 'Content-Type': 'application/json', 'Access-Control-Allow-Origin': '*' }
            }
        )
    } catch (error: any) {
        console.error('[Courses] 查詢課程列表失敗:', error)
        return new Response(
            JSON.stringify({
                success: false,
                message: '獲取課程列表失敗',
                details: error.message
            }),
            {
                status: 500,
                headers: { 'Content-Type': 'application/json', 'Access-Control-Allow-Origin': '*' }
            }
        )
    }
}

// 處理 OPTIONS 請求（CORS 預檢）
export async function onRequestOptions(): Promise<Response> {
    return new Response(null, {
        status: 204,
        headers: {
            'Access-Control-Allow-Origin': '*',
            'Access-Control-Allow-Methods': 'GET, POST, PUT, DELETE, OPTIONS',
            'Access-Control-Allow-Headers': 'Content-Type, Authorization',
            'Access-Control-Max-Age': '86400'
        }
    })
}